.c-tool {
  margin: 1em;
  margin-bottom: 2em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 2fr 50px;
  color: #6635CA;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  font-weight: bold;
  /* Firefox < 16 */
  /* Safari, Chrome and Opera > 12.1 */
  /* Internet Explorer */
  /* Opera < 12.1 */ }
  .c-tool .c-tool-icon {
    grid-column: 1; }
  .c-tool .c-tool-name {
    grid-column: 2; }
  .c-tool .c-tool-select {
    grid-column: 3; }
    .c-tool .c-tool-select:hover {
      cursor: pointer; }
  .c-tool .d-dropdown {
    grid-column: 3;
    cursor: pointer; }
  .c-tool .d-hidden {
    display: none; }
  .c-tool .c-tool-options {
    display: grid;
    grid-template-rows: 50px 50px;
    grid-template-columns: 1fr 1fr; }
    .c-tool .c-tool-options .c-tool-options-ygotext {
      grid-row: 1;
      grid-column: 1; }
    .c-tool .c-tool-options .c-tool-options-ygoicon {
      grid-row: 2;
      grid-column: 1;
      cursor: pointer; }
    .c-tool .c-tool-options .c-tool-options-pkmntext {
      grid-row: 1;
      grid-column: 2; }
    .c-tool .c-tool-options .c-tool-options-pkmnicon {
      grid-row: 2;
      grid-column: 2;
      cursor: pointer; }
  .c-tool .c-tool-card {
    display: grid;
    grid-template-columns: 1fr 1fr 40px;
    grid-template-rows: 40px 40px 280px 1fr;
    padding: 1.5em;
    -webkit-animation: fadein 2.5s;
    /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 2.5s;
    /* Firefox < 16 */
    -ms-animation: fadein 2.5s;
    /* Internet Explorer */
    -o-animation: fadein 2.5s;
    /* Opera < 12.1 */
    animation: fadein 2.5s; }
    .c-tool .c-tool-card .c-tool-card-input {
      grid-column: 1 / span 2;
      grid-row: 1; }
    .c-tool .c-tool-card .c-tool-card-search {
      grid-column: 3;
      grid-row: 1;
      justify-self: end;
      cursor: pointer; }
    .c-tool .c-tool-card .c-tool-card-cardname {
      grid-column: 1 / span 3;
      grid-row: 2; }
    .c-tool .c-tool-card .c-tool-card-cardimage {
      grid-column: 1 / span 3;
      grid-row: 3;
      align-self: center;
      justify-self: center; }
    .c-tool .c-tool-card .c-tool-card-cardimage-pkmn {
      grid-column: 1 / span 3;
      grid-row: 3 / span 4;
      align-self: center;
      justify-self: center;
      padding: 1em; }
    .c-tool .c-tool-card .c-tool-card-desc {
      grid-column: 1 / span 3;
      grid-row: 4;
      text-align: left;
      color: #555; }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-moz-keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-ms-keyframes fadein {
  .c-tool from {
    opacity: 0; }
  .c-tool to {
    opacity: 1; } }

@-o-keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
