.c-tool {
  margin: 1em;
  margin-bottom: 2em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 2fr 50px;
  color: #6635CA;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  font-weight: bold; }
  .c-tool .c-tool-icon {
    grid-column: 1; }
  .c-tool .c-tool-name {
    grid-column: 2; }
  .c-tool .c-tool-select {
    grid-column: 3; }
    .c-tool .c-tool-select:hover {
      cursor: pointer; }
  .c-tool .c-tool-bg {
    background-color: #F2F3FD;
    grid-column: 1/4; }
  .c-tool .c-tool-diceoptions {
    padding-bottom: 1em; }
  .c-tool #DFour {
    cursor: pointer; }
    .c-tool #DFour:hover path {
      cursor: pointer;
      fill: #FF1010; }
  .c-tool #DSix {
    cursor: pointer; }
    .c-tool #DSix:hover path {
      cursor: pointer;
      fill: #0051F2; }
  .c-tool #DEight {
    cursor: pointer; }
    .c-tool #DEighthover path {
      fill: #00C166; }
  .c-tool #DTwelve {
    cursor: pointer; }
    .c-tool #DTwelvehover path {
      fill: #E88400; }
  .c-tool #DTwenty {
    cursor: pointer; }
    .c-tool #DTwentyhover path {
      cursor: pointer;
      fill: #7D2AFA; }
  .c-tool .d-dropdown {
    grid-column: 3;
    cursor: pointer; }
  .c-tool .d-hidden {
    display: none; }
  .c-tool #diceSVG {
    cursor: pointer; }
