.c-tool {
  margin: 1em;
  margin-bottom: 2em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 2fr 50px;
  color: #6635CA;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  font-weight: bold; }
  .c-tool .c-tool-icon {
    grid-column: 1; }
  .c-tool .c-tool-name {
    grid-column: 2; }
  .c-tool .c-tool-select {
    grid-column: 3; }
    .c-tool .c-tool-select:hover {
      cursor: pointer; }
  .c-tool .c-tool-bg {
    background-color: #F2F3FD;
    grid-column: 1/4; }
  .c-tool .c-tool-diceoptions {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    padding: 1em; }
    .c-tool .c-tool-diceoptions .c-tool-diceoptions-tab {
      margin: 0.4em; }
      .c-tool .c-tool-diceoptions .c-tool-diceoptions-tab:hover {
        cursor: pointer; }
    .c-tool .c-tool-diceoptions .twenty {
      grid-column: 3; }
      .c-tool .c-tool-diceoptions .twenty:hover path {
        fill: #a73cff; }
    .c-tool .c-tool-diceoptions .twelve:hover path {
      fill: #ff9100; }
    .c-tool .c-tool-diceoptions .eight:hover path {
      fill: #0f9655; }
    .c-tool .c-tool-diceoptions .six:hover path {
      fill: #1160fd; }
    .c-tool .c-tool-diceoptions .four:hover path {
      fill: #ff2225; }
  .c-tool .d-dropdown {
    grid-column: 3;
    cursor: pointer; }
  .c-tool .d-hidden {
    display: none; }
  .c-tool .c-tool-multiRoll {
    display: grid;
    grid-template-columns: 150px 150px 150px;
    grid-template-rows: 1fr 1fr;
    align-content: center;
    justify-content: center; }
    .c-tool .c-tool-multiRoll .c-tool-multiRoll-dice {
      justify-self: center;
      padding: 1em; }
      .c-tool .c-tool-multiRoll .c-tool-multiRoll-dice:hover {
        cursor: pointer; }

@media (max-width: 950px) {
  .c-tool-multiRoll {
    grid-template-columns: 150px 150px !important;
    grid-template-rows: 1fr 1fr 1fr !important; } }
