.c-tool {
  margin: 1em;
  margin-bottom: 2em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 2fr 50px;
  color: #6635CA;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  font-weight: bold; }
  .c-tool .c-tool-icon {
    grid-column: 1;
    padding-left: 2px; }
  .c-tool .c-tool-name {
    grid-column: 2; }
  .c-tool .c-tool-select {
    grid-column: 3; }
    .c-tool .c-tool-select:hover {
      cursor: pointer; }
  .c-tool .c-tool-bg {
    background-color: #F2F3FD;
    grid-column: 1/4; }
  .c-tool .c-tool-options {
    padding-bottom: 1em; }
  .c-tool #DFour:hover path {
    cursor: pointer;
    fill: #FF1010; }
  .c-tool #DSix:hover path {
    cursor: pointer;
    fill: #0051F2; }
  .c-tool #DEight:hover path {
    cursor: pointer;
    fill: #00C166; }
  .c-tool #DTwelve:hover path {
    cursor: pointer;
    fill: #E88400; }
  .c-tool #DTwenty:hover path {
    cursor: pointer;
    fill: #7D2AFA; }
  .c-tool .d-dropdown {
    grid-column: 3;
    cursor: pointer; }
  .c-tool .d-hidden {
    display: none; }
  .c-tool #FancyCoin {
    cursor: pointer; }
