.c-tcg {
  padding-top: 5em;
  grid-template-columns: 1fr 1fr; }
  .c-tcg .c-tcg-header {
    background-color: #6635CA;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-weight: bold;
    color: #FFF;
    font-size: 1.2em; }
    .c-tcg .c-tcg-header .c-tcg-header-return {
      grid-column: 1;
      align-self: center;
      justify-self: start;
      padding-left: 1.5em; }
      .c-tcg .c-tcg-header .c-tcg-header-return:hover {
        cursor: pointer; }
    .c-tcg .c-tcg-header .c-tcg-header-title {
      grid-column: 2; }
  .c-tcg .c-tcg-tools {
    display: grid;
    grid-template-columns: 1fr 1fr; }

@media (max-width: 900px) {
  .c-tcg-tools {
    display: grid;
    grid-template-columns: 1fr !important;
    grid-template-rows: none !important; } }
