.bg-container {
  position: relative; }
  .bg-container .bg-container-background {
    display: flex; }
  .bg-container .bg-container-title {
    position: absolute;
    top: 8%;
    margin: 0 auto;
    left: 0;
    right: 0;
    color: #FFF;
    font-size: 4em; }
  .bg-container .bg-container-icon {
    position: absolute;
    top: 8%;
    padding: 2.5em;
    margin: 0 auto;
    left: 0;
    right: 0; }
  .bg-container .bg-container-desc {
    position: absolute;
    width: 70%;
    text-align: left;
    display: block;
    margin: 0 auto;
    padding: 3em;
    font-size: 1.2em;
    line-height: 2em;
    font-weight: bold;
    left: 0;
    right: 0;
    top: 37%;
    color: #FFF; }
  .bg-container .bg-container-button {
    position: absolute;
    top: 65%;
    display: block;
    text-align: center;
    margin: 0 auto;
    left: 0;
    right: 0; }
    .bg-container .bg-container-button button {
      background: none;
      border: none; }
      .bg-container .bg-container-button button:hover svg {
        cursor: pointer;
        transform: scale(1.1); }
  .bg-container .bg-container-next {
    position: absolute;
    top: 78%;
    padding: 2.5em;
    margin: 0 auto;
    left: 0;
    right: 0; }
    .bg-container .bg-container-next:hover {
      cursor: pointer; }

@media (min-width: 2000px) {
  .bg-container-desc {
    font-size: 2.5em !important;
    top: 25% !important; } }

@media (min-width: 1500px) {
  .bg-container-desc {
    font-size: 1.8em !important;
    top: 32% !important; } }

@media (max-width: 1000px) {
  .bg-container-desc {
    font-size: 0.9em !important;
    padding: 3em !important; }
  .bg-container-title {
    top: 2% !important; }
  .bg-container-icon {
    top: 2% !important; }
  .bg-container-next {
    top: 74% !important; }
  .bg-container-desc {
    top: 39% !important;
    font-size: 0.9em !important; } }

@media (min-width: 850px) {
  #brdBackgroundMobile {
    display: none !important; } }

@media (max-width: 850px) {
  #bgBackground {
    display: none !important; }
  .bg-container-desc {
    top: 30% !important; } }
