.c-tool {
  margin: 1em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 2fr 50px;
  color: #6635CA;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  box-shadow: 0 14px 43px rgba(116, 116, 116, 0.507);
  font-size: 1.1em; }
  .c-tool .c-tool-icon {
    grid-column: 1; }
  .c-tool .c-tool-name {
    grid-column: 2;
    outline: none; }
  .c-tool .c-tool-lifepoints {
    display: grid;
    grid-template-rows: 40px 90px 60px 60px 60px 60px 60px;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 2em; }
    .c-tool .c-tool-lifepoints .c-tool-lifepoints-selflife {
      grid-column: 1;
      background-color: #6097CA;
      color: #FFF;
      padding: 0.2em;
      border-radius: 4px;
      margin: 0.05em;
      font-size: 1.4em; }
    .c-tool .c-tool-lifepoints .c-tool-lifepoints-total {
      grid-column: 2;
      background-color: #FFF;
      padding: 0.2em;
      border-radius: 4px;
      margin: 0.05em;
      font-size: 1.4em; }
    .c-tool .c-tool-lifepoints .c-tool-lifepoints-opplife {
      grid-column: 3;
      background-color: #CA6060;
      color: #FFF;
      padding: 0.2em;
      border-radius: 4px;
      margin: 0.05em;
      font-size: 1.4em; }
    .c-tool .c-tool-lifepoints .c-tool-lifepoints-options {
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 100%;
      grid-column: 1 / span 3;
      grid-row: 2;
      display: grid; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-selfplus {
        background-color: #51C48E;
        color: #FFF;
        margin: 0.2em;
        border-radius: 4px;
        justify-self: center;
        align-self: center; }
        .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-selfplus:hover {
          cursor: pointer;
          background-color: #00a759; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-selfminus {
        background-color: #CA6060;
        color: #FFF;
        margin: 0.2em;
        border-radius: 4px;
        justify-self: center;
        align-self: center; }
        .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-selfminus:hover {
          cursor: pointer;
          background-color: #c20000; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-oppnplus {
        background-color: #51C48E;
        color: #FFF;
        margin: 0.2em;
        border-radius: 4px;
        justify-self: center;
        align-self: center; }
        .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-oppnplus:hover {
          cursor: pointer;
          background-color: #00a759; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-oppnminus {
        background-color: #CA6060;
        color: #FFF;
        margin: 0.2em;
        border-radius: 4px;
        justify-self: center;
        align-self: center; }
        .c-tool .c-tool-lifepoints .c-tool-lifepoints-options .c-tool-lifepoints-options-oppnminus:hover {
          cursor: pointer;
          background-color: #c20000; }
    .c-tool .c-tool-lifepoints .c-tool-lifepoints-value {
      background-color: #ae82f5;
      margin: 0.2em;
      font-size: 2.1em;
      font-weight: normal;
      color: #FFF;
      border-radius: 4px; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-value:hover {
        cursor: pointer;
        background-color: #864ce4; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-value span {
        display: inline-block;
        width: 100%;
        height: 100%;
        text-align: center; }
    .c-tool .c-tool-lifepoints .c-tool-lifepoints-value-refresh {
      background-color: #E88641;
      color: #FFF;
      margin: 0.2em;
      padding: 0.5em;
      border-radius: 4px;
      grid-column: 2;
      grid-row: 7;
      justify-self: center;
      align-self: center; }
      .c-tool .c-tool-lifepoints .c-tool-lifepoints-value-refresh:hover {
        cursor: pointer;
        background-color: #e7680d; }
  .c-tool .c-tool-select {
    grid-column: 3; }
    .c-tool .c-tool-select:hover {
      cursor: pointer; }

