.c-modal {
  position: fixed;
  background-color: #FFF;
  opacity: 0.96;
  width: 100%;
  height: 100%;
  margin: 0 !important;
  left: 0;
  right: 0;
  z-index: 1;
  bottom: 0; }
  .c-modal .c-modal-button {
    padding: 2em;
    border: none;
    background: none; }
    .c-modal .c-modal-button button {
      cursor: pointer; }
      .c-modal .c-modal-button button:hover svg {
        transform: scale(1.1); }
  .c-modal button {
    border: none; }
    .c-modal button:hover {
      cursor: pointer; }
  .c-modal .c-modal-themes-options {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    max-width: 40%;
    margin-left: auto;
    margin-right: auto; }
    .c-modal .c-modal-themes-options .c-modal-themes-options-select {
      padding: 1.5em;
      margin: 1em;
      margin-left: auto;
      margin-right: auto; }
      .c-modal .c-modal-themes-options .c-modal-themes-options-select:hover {
        cursor: pointer; }
    .c-modal .c-modal-themes-options .dark-theme {
      background-color: #555;
      color: #FFF;
      border: 2px #CCC solid; }
      .c-modal .c-modal-themes-options .dark-theme:hover {
        background-color: #383838; }
    .c-modal .c-modal-themes-options .light-theme {
      background-color: #FFF;
      color: #555;
      border: 2px #555 solid; }
      .c-modal .c-modal-themes-options .light-theme:hover {
        background-color: #d8d8d8; }
    .c-modal .c-modal-themes-options .red-theme {
      background-color: #ff5438;
      color: #FFF;
      border: 2px #555 solid; }
      .c-modal .c-modal-themes-options .red-theme:hover {
        background-color: #ff3516; }
    .c-modal .c-modal-themes-options .blue-theme {
      background-color: #6097CA;
      color: #FFF;
      border: 2px #555 solid; }
      .c-modal .c-modal-themes-options .blue-theme:hover {
        background-color: #097ae4; }
  .c-modal .c-modal-exit:hover {
    cursor: pointer; }

@media (max-width: 700px) {
  .c-modal-themes-options {
    max-width: none !important; } }

.c-modal-login {
  position: absolute;
  background-color: #D1DCF3;
  width: 100%;
  height: 100%;
  margin: 0 !important;
  left: 0;
  right: 0;
  top: 3.88em; }
  .c-modal-login .c-modal-login-error {
    background-color: crimson;
    color: #FFF;
    margin-right: auto;
    margin-left: auto;
    text-align: left;
    padding: 1.5em;
    display: flex;
    justify-content: center; }
  .c-modal-login .c-modal-button {
    padding: 2em;
    border: none;
    background: none;
    cursor: pointer; }
    .c-modal-login .c-modal-button:hover svg {
      transform: scale(1.1); }
  .c-modal-login button {
    border: none; }
    .c-modal-login button:hover {
      cursor: pointer; }
  .c-modal-login .c-modal-login-tabs {
    margin-top: 6em; }

.c-modal-header {
  background-color: #6097CA;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  font-weight: bold;
  color: #FFF;
  font-size: 1.2em;
  z-index: 1; }
  .c-modal-header .c-custom-header-return {
    grid-column: 1;
    align-self: center;
    justify-self: start;
    padding-left: 1.5em; }
    .c-modal-header .c-custom-header-return:hover {
      cursor: pointer; }
  .c-modal-header .c-custom-header-title {
    grid-column: 2; }

.c-modal-tabs {
  margin: 2em;
  margin-bottom: 3em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 10px 1fr;
  color: #555;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  font-weight: bold;
  max-width: 70%;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 14px 43px rgba(95, 95, 95, 0.603); }
  .c-modal-tabs:hover {
    cursor: pointer; }
  .c-modal-tabs .c-modal-tabs-social {
    grid-column: 3;
    margin-right: 3.5em; }
  .c-modal-tabs .c-modal-tabs-break {
    grid-column: 2; }
  .c-modal-tabs .c-modal-tabs-image {
    grid-column: 1;
    align-self: center;
    justify-self: center;
    padding-left: 0.5em; }

.c-custom {
  margin-top: 5em;
  margin-bottom: 5em;
  display: grid;
  grid-template-columns: 1fr 1fr; }
  .c-custom .c-custom-header {
    background-color: #6097CA;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-weight: bold;
    color: #FFF;
    font-size: 1.2em; }
    .c-custom .c-custom-header .c-custom-header-return {
      grid-column: 1;
      align-self: center;
      justify-self: start;
      padding-left: 1.5em; }
      .c-custom .c-custom-header .c-custom-header-return:hover {
        cursor: pointer; }
    .c-custom .c-custom-header .c-custom-header-title {
      grid-column: 2; }
    .c-custom .c-custom-header .c-custom-header-menu {
      grid-column: 3;
      align-self: center;
      justify-self: end;
      padding-right: 1.5em; }
      .c-custom .c-custom-header .c-custom-header-menu:hover {
        cursor: pointer; }
  .c-custom .c-custom-save {
    grid-column: 1 / span 2; }
    .c-custom .c-custom-save .c-custom-save-svg:hover {
      cursor: pointer; }
  .c-custom .c-custom-connectionlost {
    color: crimson;
    text-align: left;
    font-weight: bold;
    padding: 2.3;
    grid-column: 1 / span 2;
    grid-row: 2;
    text-align: center; }

.c-selection {
  background-color: #6097CA;
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.5em; }
  .c-selection .c-selection--button button {
    border: none;
    background: none;
    cursor: pointer; }
    .c-selection .c-selection--button button:hover svg {
      transform: scale(1.1); }

.hidden {
  visibility: hidden; }

.display-none {
  display: none; }

.display-block {
  display: block; }

@media (max-width: 800px) {
  .c-custom {
    grid-template-columns: 1fr; }
    .c-custom .c-custom-save {
      grid-column: 1; }
    .c-custom .c-custom-connectionlost {
      grid-column: 1;
      grid-row: 2; }
  .c-tool {
    max-height: 500px;
    overflow-y: scroll; } }

@media (min-width: 1280px) {
  .c-custom {
    grid-template-columns: 1fr 1fr 1fr; }
    .c-custom .c-custom-save {
      grid-column: 1 / span 3; }
    .c-custom .c-custom-connectionlost {
      grid-column: 1 / span 3;
      grid-row: 2;
      text-align: center !important; } }
