.c-tool-typeChart {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  padding: 1.5em; }
  .c-tool-typeChart .c-tool-typeChart-choose {
    grid-column: 1 / span 2;
    align-self: center;
    justify-self: start; }
  .c-tool-typeChart .c-tool-typeChart-options {
    grid-column: 3 / span 4;
    align-self: center;
    justify-self: center;
    font-size: 0.8em;
    width: 100%;
    box-sizing: border-box;
    background-color: #6636ca;
    border: 2.5px solid #FFF;
    padding: .6em 1.4em .5em .8em;
    line-height: 1.3;
    border-radius: .5em;
    color: #FFF;
    align-self: center;
    justify-self: start;
    font-weight: bold; }
  .c-tool-typeChart .c-tool-typeChart-superTitle {
    grid-column: 1 / span 4;
