.c-container {
  position: relative; }
  .c-container .c-container-background {
    display: flex; }
  .c-container .c-container-brand {
    position: absolute;
    top: 8%;
    padding: 2.5em;
    margin: 0 auto;
    left: 0;
    right: 0; }
  .c-container .c-container-desc {
    position: absolute;
    width: 70%;
    text-align: left;
    display: block;
    margin: 0 auto;
    padding: 3em;
    font-size: 1.2em;
    line-height: 2em;
    font-weight: bold;
    left: 0;
    right: 0;
    top: 35%; }
  .c-container .c-container-next {
    position: absolute;
    top: 62%;
    padding: 2.5em;
    margin: 0 auto;
    left: 0;
    right: 0; }
    .c-container .c-container-next:hover {
      cursor: pointer; }
  .c-container .c-container-homeHeader {
    padding: 0.2em;
    position: absolute;
    top: 0;
    display: block;
    background-color: #FFF;
    left: 0;
    right: 0; }
    .c-container .c-container-homeHeader .c-container-homeHeader-list {
      list-style-type: none;
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-around; }
      .c-container .c-container-homeHeader .c-container-homeHeader-list ul {
        display: inline-block;
        color: #555; }
        .c-container .c-container-homeHeader .c-container-homeHeader-list ul:hover {
          color: #68b1e6;
          cursor: pointer;
          font-weight: bold;
          text-decoration: underline; }

@media (min-width: 2000px) {
  .c-container-desc {
    font-size: 2.5em !important;
    top: 20% !important; } }

@media (min-width: 1500px) {
  .c-container-desc {
    font-size: 1.8em !important;
    top: 26% !important; } }

@media (max-width: 1000px) {
  .c-container-homeHeader {
    display: none !important; }
  .c-container-desc {
    font-size: 0.9em !important;
    padding: 3em !important; }
  .c-container-next {
    top: 70% !important; }
  .c-container-brand {
    top: 0% !important;
    padding: 0 !important; } }

@media (min-width: 850px) {
  #homeBackgroundMobile {
    display: none; } }

@media (max-width: 850px) {
