.c-tool {
  margin: 1em;
  margin-bottom: 2em;
  background-color: #FFF;
  display: grid;
  grid-template-columns: 50px 2fr 50px;
  color: #6635CA;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  font-weight: bold; }
  .c-tool .c-tool-icon {
    grid-column: 1; }
  .c-tool .c-tool-name {
    grid-column: 2; }
  .c-tool .c-tool-select {
    grid-column: 3; }
    .c-tool .c-tool-select:hover {
      cursor: pointer; }
    .c-tool .c-tool-select .d-dropdown {
      grid-column: 3;
      cursor: pointer; }
    .c-tool .c-tool-select .d-hidden {
      display: none; }
  .c-tool .c-tool-dark-background .c-tool-notes-title {
    color: #FFF; }
  .c-tool .c-tool-notes {
    display: grid;
    padding: 1.5em; }
    .c-tool .c-tool-notes .c-tool-notes-title {
      align-self: start;
      justify-self: start;
      padding: 1em; }
