.c-rpg {
  padding-top: 5em;
  grid-template-columns: 1fr 1fr; }
  .c-rpg .c-rpg-header {
    background-color: #4FC38D;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    font-weight: bold;
    color: #FFF;
    font-size: 1.2em; }
    .c-rpg .c-rpg-header .c-rpg-header-return {
      grid-column: 1;
      align-self: center;
      justify-self: start;
      padding-left: 1.5em; }
      .c-rpg .c-rpg-header .c-rpg-header-return:hover {
        cursor: pointer; }
    .c-rpg .c-rpg-header .c-rpg-header-title {
      grid-column: 2; }
  .c-rpg .c-rpg-tools {
    display: grid;
    grid-template-columns: 1fr 1fr; }
  @media (max-width: 900px) {
    .c-rpg .c-rpg-tools {
      display: grid;
      grid-template-columns: 1fr !important;
      grid-template-rows: none !important; } }
